:root {
  --dark-green: #466060;
  --dark-green-lighter: #5d8181;
  --dark-grey: #333;
  --light-green: #679695;
  --white: #FFFFFF;
}

.App {
  font-family:'Courier New', Courier, monospace;
}

h1 {
  font-size: 56px;
}

h2 {
  font-size: 28px;
  font-weight: 500;
}

h3 {
  font-size: 48px;
  font-weight: 600;
}

p {
  font-size: 20px;
}

button {
  font-family:'Courier New', Courier, monospace;
}

.dark {
  background-color: var(--dark-green);
  color: var(--dark-grey);
}

.light {
  background-color: var(--light-green);
  color: var(--white);
}

.dark-reverse {
  background-color: var(--dark-grey);
  color: var(--dark-green);
}

.light-reverse {
  background-color: var(--white);
  color: var(--light-green);
}

.navbar {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 100;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-end;
  height: 64px;
  padding: 0;
  margin: 0;
}

.menu {
  display: flex;
  flex-direction: row;
  list-style-type: none;
  align-items: center;
  margin: 0;
  padding: 0;
}

.menu ul {
  display: flex;
  list-style: none;
  margin: 0;
  padding: 0;
}

.menu li {
  margin: 0 24px 0 0;
  cursor: pointer;
  overflow: hidden;
  font-weight: bold;
  font-size: 24px;
  height: 24px;
}

.menu ul a {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  font-size: 24px;
  margin: 0 16px 0 0;
  cursor: pointer;
}

.menu li:nth-last-child(-n + 2) {
  margin: 0 8px 0 0;
}

.menu li:last-child {
  margin: 0 24px 0 0;
}

.light-reverse ul a {
  color:var(--light-green);
}

.dark-reverse ul a {
  color:var(--dark-green);
}

.menu ul a:hover {
  text-decoration: underline;
}

.menu-button-container {
  margin: 0 16px 0 0;
  display: none;
  height: 100%;
  width: 30px;
  cursor: pointer;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

#menu-toggle {
  display: none;
}

.menu-button,
.menu-button::before,
.menu-button::after {
  display: block;
  position: absolute;
  height: 4px;
  width: 30px;
  transition: transform 400ms cubic-bezier(0.23, 1, 0.32, 1);
  border-radius: 2px;
}

.light-reverse .menu-button,
.light-reverse .menu-button::before,
.light-reverse .menu-button::after {
  background-color: var(--light-green);
}

.dark-reverse .menu-button,
.dark-reverse .menu-button::before,
.dark-reverse .menu-button::after {
  background-color: var(--dark-green);
}

.menu-button::before {
  content: '';
  margin-top: -8px;
}

.menu-button::after {
  content: '';
  margin-top: 8px;
}

#menu-toggle:checked + .menu-button-container .menu-button::before {
  margin-top: 0px;
  transform: rotate(405deg);
}

.light-reverse #menu-toggle:checked + .menu-button-container .menu-button {
  background: var(--white);
}

.dark-reverse #menu-toggle:checked + .menu-button-container .menu-button {
  background: var(--dark-grey);
}

#menu-toggle:checked + .menu-button-container .menu-button::after {
  margin-top: 0px;
  transform: rotate(-405deg);
}

@media (max-width: 700px) {

  .navbar {
    gap: 16px;
  }

  .menu-button-container {
    display: flex;
  }

  .menu {
    position: absolute;
    top: 0;
    margin-top: 64px;
    left: 0;
    flex-direction: column;
    width: 100%;
    justify-content: center;
    align-items: center;
  }

  #menu-toggle ~ .menu li {
    height: 0;
    margin: 0;
    padding: 0;
    border: 0;
    transition: height 400ms cubic-bezier(0.23, 1, 0.32, 1);
  }

  #menu-toggle:checked ~ .menu li {
    height: 56px;
    padding: 0;
    transition: height 400ms cubic-bezier(0.23, 1, 0.32, 1);
  }
  
  .dark-reverse #menu-toggle:checked ~ .menu li {
    border-color: var(--dark-green);
    border-style: solid;
    border-width: 1px 0px 0px 0px;
  }

  .light-reverse #menu-toggle:checked ~ .menu li {
    border-color: var(--light-green);
    border-style: solid;
    border-width: 1px 0px 0px 0px;
  }

  .menu > li {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0;
    padding: 0;
    width: 100%;
  }
  
  .light-reverse .menu > li {
    color: var(--light-green);
    background-color: var(--white);
  }

  .dark-reverse .menu > li {
    color: var(--dark-green);
    background-color: var(--dark-grey);
  }
}

/* toggle switch dark theme */
.toggle-theme {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  margin-right: 16px;
}

.toggle-theme .dark {
  background-color: var(--dark-grey);
}

.theme-switch {
  position: relative;
  display: inline-block;
  width: 40px;
  height: 24px;
}

.theme-switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: var(--light-green);
  -webkit-transition: .4s;
  transition: .4s;
  border-radius: 34px;
}

.slider:before {
  position: absolute;
  content: "";
  height: 20px;
  width: 20px;
  left: 2px;
  bottom: 2px;
  background-color: var(--white);
  -webkit-transition: .4s;
  transition: .4s;
  border-radius: 50%;
}

input:checked + .slider {
  background-color: var(--dark-green);
}

input:focus + .slider {
  box-shadow: 0 0 1px var(--dark-green);
}

input:checked + .slider:before {
  -webkit-transform: translateX(16px);
  -ms-transform: translateX(16px);
  transform: translateX(16px);
  background-color: var(--dark-grey);
}

.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

.page {
  z-index: -1;
  width: 100%;
