}

#projects {
  display: flex;
  flex-direction: column;
  margin-top: 256px;
}

#projects .title {
  margin-left: 160px;
  margin-right: 160px;
}

#projects .containers {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  margin: 144px auto;
  padding: 0;
  grid-gap: 64px;
  text-align: center;
  align-items: center;
}

#projects .containers .container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  z-index: 10;
  height: 440px;
  width: 480px;
  padding: 32px;
  border-radius: 4px;
	transition: all 0.3s;
	position: relative;
  cursor: pointer;
  gap: auto;
}

.container-up {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

.container-down {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}

.dark #projects .containers .container {
  color: var(--dark-grey);
}

.light #projects .containers .container {
  color: var(--white);
}

#projects .container span {
	transition: all 0.3s;
}

#projects .container::before {
	content: '';
	position: absolute;
	bottom: 0;
	left: 0;
	width: 100%;
	height: 100%;
	z-index: 1;
	opacity: 0;
	transition: all 0.3s;
	border-top-width: 2px;
	border-bottom-width: 2px;
	border-top-style: solid;
	border-bottom-style: solid;
	border-top-color: rgba(255,255,255,0.5);
	border-bottom-color: rgba(255,255,255,0.5);
	transform: scale(0.1, 1);
}

#projects .container:hover span {
	letter-spacing: 2px;
}

#projects .container:hover::before {
	opacity: 1;	
	transform: scale(1, 1);	
  background-size: 100%;
  background-repeat: no-repeat;
  z-index: 5;
}

#projects .container::after {
	content: '';
	position: absolute;
	bottom: 0;
	left: 0;
	width: 100%;
	height: 100%;
	z-index: 1;
	transition: all 0.4s;
  background-color: rgb(255, 255, 255, 0.2);
}

#projects .containers .container:hover::after {
	opacity: 0;	
	transform: scale(0.1, 1);
}

#projects .container h3 {
  font-weight: 800;
  margin: 0;
  margin-bottom: 16px;
}

#projects .container p {
  font-weight: 400;
  line-height: 32px;
  margin: 0 16px 16px 16px;
}

#projects .used-tech-title p {
  font-weight: 400;
  line-height: 32px;
