.bottom-button {
  position: fixed;
  bottom: 32px;
  right: 16px;
  height: 64px;
}

.bottom-button button {
  z-index: 100;
  font-weight: 600;
  font-size: 32px;
  box-shadow: rgba(17, 17, 26, 0.1) 0px 4px 16px, rgba(17, 17, 26, 0.1) 0px 8px 24px, rgba(17, 17, 26, 0.1) 0px 16px 56px;
  border-radius: 100%;
  border: none;
  outline: none;
  width: 64px;
  height: 64px;
}

.bottom-button button:hover {
  cursor: pointer;
  animation: bounce 2s infinite;
}

@keyframes bounce {
  0% { transform: scale(1,1) translate(0, 0); }
  20% { transform: scale(1,0.8) translate(0, 24px); }
  60% { transform: scale(1,1.1) translate(0, -16px); }
  80% { transform: scale(1,0.8) translate(0, 24px); }
  100% { transform: scale(1,1) translate(0, 0); }
}

